.container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #432874;
  padding: 1rem;
}

.loginBox {
  background-color: white;
  padding: 2.5rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}

.loginBox h2 {
  color: #432874;
  text-align: center;
  margin-bottom: 2rem;
  font-size: 1.8rem;
  font-weight: bold;
}

.inputGroup {
  margin-bottom: 1.5rem;
}

.inputGroup input {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 4px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.inputGroup input:focus {
  outline: none;
  border-color: #432874;
}

.passwordWrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.passwordWrapper input {
  padding-right: 40px;
}

.eyeIcon {
  position: absolute;
  right: 10px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 5px;
  font-size: 1.2rem;
}

.loginButton {
  width: 100%;
  padding: 0.8rem;
  background-color: #432874;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-bottom: 1.5rem;
}

.loginButton:hover {
  background-color: #351d63;
}

.signupLink {
  text-align: center;
  color: #666;
  font-size: 0.9rem;
}

.signupLink a {
  color: #432874;
  text-decoration: none;
  font-weight: bold;
  margin-left: 0.3rem;
}

.signupLink a:hover {
  text-decoration: underline;
}

.signupButton {
  background: none;
  border: none;
  color: #432874;
  font-weight: bold;
  cursor: pointer;
  padding: 0;
  margin-left: 0.3rem;
  font-size: inherit;
}

.signupButton:hover {
  text-decoration: underline;
}
